<template>
  <div class="add-reg-manage">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      class="form-content"
    >
      <FormItem label="任务编号" prop="step_num" >
        <Input v-model="formValidate.step_num" placeholder="请输入任务编号" :disabled="disableFlg"></Input>
      </FormItem>
      <FormItem label="调度系统编号" prop="system_num" >
        <Input v-model="formValidate.system_num" placeholder="请输入调度系统编号" ></Input>
      </FormItem>
      <FormItem label="任务依赖" prop="task_dependence" >
        <Input v-model="formValidate.task_dependence" placeholder="请输入任务依赖" :disabled="disableFlg"></Input>
      </FormItem>
       <FormItem label="执行开始时间" prop="date_from" v-show="!disableFlg">
        <DatePicker
          type="date"
          placeholder="请选择执行开始时间"
          :value="formValidate.date_from"
          @on-change="formValidate.date_from=$event"
          
        ></DatePicker>
      </FormItem>
      <FormItem label="执行结束时间" prop="date_to"  v-show="!disableFlg">
        <DatePicker
          type="date"
          placeholder="请选择执行结束时间"
          :value="formValidate.date_to"
          @on-change="formValidate.date_to=$event"
         
        ></DatePicker>
      </FormItem>
      <FormItem label="服务方法" prop="service" v-show="!disableFlg">
        <Input v-model="formValidate.service" placeholder="请输入服务方法" ></Input>
      </FormItem>
      <FormItem label="参数方法" prop="args" v-show="!disableFlg">
        <Input v-model="formValidate.args" placeholder="请输入参数方法" ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  data() {
    const checkChinese = (rule, value, callback) => {
      if (value) {
        if (/[^\d]/g.test(value)) {
          callback(new Error("任务编号只能为数字"));
        } else {
          callback();
        }
      }
      callback();
    };
    const checkChinese2 = (rule, value, callback) => {
      if (value) {
        if (/[^\d]/g.test(value)) {
          callback(new Error("调度系统编号只能为数字"));
        } else {
          callback();
        }
      }
      callback();
    };
    const checkChinese3 = (rule, value, callback) => {
      if (value) {
        if (/[^\d]/g.test(value)) {
          callback(new Error("任务依赖只能为数字"));
        } else {
          callback();
        }
      }
      callback();
    };
    const checkChinese4 = (rule, value, callback) => {
      if (value) {
        if (/[\u4E00-\u9FA5]/g.test(value)) {
          callback(new Error("服务方法只能为数字或字母"));
        } else {
          callback();
        }
      }
      callback();
    };
    const checkChinese5 = (rule, value, callback) => {
      if (value) {
        if (/[\u4E00-\u9FA5]/g.test(value)) {
          callback(new Error("参数方法只能为数字或字母"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      fileObj:{},
      uploadList: [],
      fileId:"",
      disableFlg:false,
      formValidate: {
        step_num: "", //任务编号
        system_num: "", //调度系统编号
        task_dependence: "", //任务依赖
        date_from: "", //执行开始时间
        date_to: "", //执行结束时间
        service: "", //服务方法
        args: "", //参数方法
      },
      index: 1,
      ruleValidate: {
        step_num: [
          {
            required: true,
            message: "任务编号不能为空！",
            trigger: "blur"
          },
          { validator: checkChinese, trigger: "blur" }
        ],
        system_num: [
          {
            required: true,
            message: "调度系统编号不能为空！",
            trigger: "blur"
          },
          { validator: checkChinese2, trigger: "blur" }
        ],
        task_dependence: [
          {
            required: false,
            message: "任务依赖不能为空！",
            trigger: "blur"
          },
          { validator: checkChinese3, trigger: "blur" }
        ],
        date_from: [
          {
            required: false,
            message: "执行开始时间不能为空！",
            trigger: "change"
          }
        ],
        date_to: [
          {
            required: false,
            message: "执行结束时间不能为空！",
            trigger: "change"
          }
        ],
        service: [
          {
            required:false,
            message: '服务方法不能超过一千字',
            trigger: 'blur',
          },
          { validator: checkChinese4, trigger: "blur" }
          
        ],
        args: [
          {
            required:false,
            message: '参数方法不能超过一千字',
            trigger: 'blur',
          },
          { validator: checkChinese5, trigger: "blur" }
        ],
      }
    };
  },
  created() {
    // 1:新增 2修改
    if (this.dataObj.type == "2") {
      this.disableFlg = true;
      let datas = this.dataObj.data;
      if(datas && datas.step_num){
        this.getInfInfoRow(datas);
      }
    } 
  },
  mounted() {},
  methods: {
    getInfInfoRow(datas) {
      this.formValidate = {
        step_num: datas.step_num, //任务编号
        system_num: datas.system_num, //调度系统编号
        task_dependence: datas.task_dependence, //任务依赖
        date_from: datas.date_from, //执行开始时间
        date_to: datas.date_to, //执行结束时间
        service: datas.service, //服务方法
        args: datas.args, //参数方法
      }
    },
    changeUseSts(flg) {
      this.formValidate.ifShow = flg;
    },
    /**
     * name:保存新增信息
     **/
    handleSubmit(name) {
      console.log(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
          // 新增
          if(this.dataObj.type == "1"){
            this.axios
              .post("/system/taskInfo2/save", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$emit("save");
                  this.$Message.success("保存成功！");
                }
              })
              .catch(error => {});
          }else{
            // 修改
            this.axios
              .post(`/system/taskInfo2/update/${this.formValidate.step_num}/${this.formValidate.system_num}`, this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.success("修改成功！");
                  this.$emit("save");
                }
              })
              .catch(error => {});
          }
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-reg-manage {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .special-div {
      width: 100px;
      font-size: 14px;
      padding: 10px 12px 10px 0;
      text-align: right;
    }
    .special-form {
      margin: 0 0 20px -20px;
      border: 1px solid #eeeeee;
      padding: 20px 20px 0px 0px;
    }
  }
}
</style>


