<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">数据集成</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">全公司流程管理</span>
      <!-- <div class="button-content">
        <Button type="primary" @click="operate('1')">
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
      </div> -->
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="任务编号："  prop="stepNum">
                <Input v-model="formValidate.stepNum" placeholder="请输入任务编号" style="width:200px"></Input>
              </FormItem>
              <FormItem label="任务名称："  prop="stepName">
                <Input v-model="formValidate.stepName" placeholder="请输入任务名称" style="width:200px"></Input>
              </FormItem>
              <Row>
                <div class="btnClass">
                  <Button
                    type="primary"
                    @click="handleSubmit(0, 1)"
                  >查询</Button>
                </div>
              </Row>
            </Row>
          </Form>
          <div class="contentCellBody">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <Row>
              <table class="tableBox">
                <thead>
                  <tr>
                    <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                  </tr>
                </thead>
                <tbody>
                  <template>
                    <tr v-for="(item,index) in tableBody" :id="item.documentId">
                      <td>
                        {{pageSize*(pageNum-1)+index+1}}
                      </td>
                      <td class="noWrap">
                        <span class="spanBox" @click="operate('2', item)">修改</span>
                        <span class="spanBox" @click="operate('3',item)">删除</span>
                      </td>
                      <td>{{item.step_num}}</td>
                      <td>{{item.system_num}}</td>
                      <td>{{item.step_name}}</td>
                      <td>{{item.task_dependence}}</td>
                      <td>{{item.date_from}}</td>
                      <td>{{item.date_to}}</td>
                      <td>{{item.service}}</td>
                      <td>{{item.args}}</td>
                    </tr> 
                  </template>
                </tbody>
              </table>
            </Row>
          </div>
          <div class="flexCenter pageBox">
            <Page :total="rowCount" 
            :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
            :current.sync="currentpage"
            show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <addProcessCompanyManage  ref="dialogModal" :key="keyId" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%"></addProcessCompanyManage>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addProcessCompanyManage from "./addProcessCompanyManage"; //添加和修改

export default {
  components:{addProcessCompanyManage},
  mounted() {
    this.handleSubmit(0, 1)
  },
  data() {
    const checkChinese = (rule, value, callback) => {
      if (value) {
        if (/[^\d]/g.test(value)) {
          callback(new Error("任务编号只能为数字"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      tableHeader:[
        {name:"序"},
        {name:"操作"},
        {name:"任务编号"},
        {name:"调度系统编号"},
        {name:"任务名称"},
        {name:"任务依赖"},
        {name:"执行开始时间"},
        {name:"执行结束时间"},
        {name:"服务方法"},
        {name:"参数方法"},
      ],
      tableBody:[],
      pageSize:10,
      pageNum:1,
      rowCount:0,
      currentpage:1,
      keyId:"",
      modelTitle:'新增',
      tabview: "",
      showModal: false,
      dataObj:{
        type:'1',//1新增 2 修改 3删除
      },
      stepNameList:[],//任务结果列表
      formValidate: {
        stepNum: "", //任务编号
        stepName:"", // 任务名称
      },
      ruleValidate: {
        stepNum: [
          { required: false, validator: checkChinese, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    /**
     * name:获取下拉字典
     */
    getDic(){
      this.axios
      .get("/system/getDic/STEP_STATUS")
      .then(response => {
        if (response.code == "200") {
          this.stepNameList = response.data.STEP_STATUS;
        }
      })
      .catch(function(error) {});
    },
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        ifExacte:ifExacte,
        stepNum:this.formValidate.stepNum,
        stepName:this.formValidate.stepName,
        pageNum:pageNo,
        pageSize:this.pageSize
      }
      this.$refs.formValidate.validate(valid => {
        if(valid){
          this.axios
          .post("/system/taskInfo2/listSearch", data, {
              headers: { "Content-Type": "application/json" }
            })
          .then(response => {
            if (response.code == "200") {
              this.tableBody = response.data.records;
              this.rowCount = response.data.total;
            }
          })
          .catch(error => {});
        }
      })
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
   operate(type, item) {
     console.log('item', item)
      switch (type) {
        case "1":
          this.keyId = !this.keyId;
          this.modelTitle = "新增";
          this.showModal = true;
          this.dataObj = {
            type: 1,
            data: [],
          };
          break;
        case "2":
          this.keyId = item.step_num;
          this.modelTitle = "修改";
          this.dataObj = {
            type: 2,
            data: item,
          };
          this.showModal = true;
          break;
        case "3":
          this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.deleteItem(item);
            },
            onCancel: () => {}
          });
          break;
      }
    },
    deleteItem(item){
      console.log('item', item);
      if(item.step_num){
        this.axios
        .post(`/system/taskInfo2/delete/${item.step_num}`,{
            headers: { "Content-Type": "application/json" }
          })
          .then(response =>{
            this.$Message.success("删除成功！");
            this.handleSubmit(0, 1);
            this.currentpage = 1;
            this.pageNum = 1;
          })
          .catch(error=>{
            console.log(error);
          })
      }
    },
    saveDatas() {
      this.showModal = false;
      this.pageNum = 1;
      this.currentpage = 1;
      this.handleSubmit(0,1);
    },
    cancelDialog(val) {
      this.showModal = val;
      this.cancel();
    },
    cancel(){
      if(this.$refs['dialogModal'].$refs['formValidate']){
        this.$refs['dialogModal'].$refs['formValidate'].resetFields();
      }
    }
  }
};
</script>
<style scoped>
</style>



